@import url(https://fonts.googleapis.com/css?family=Abril+Fatface|Martel+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.reax-app {
  font-family: "Martel Sans", sans-serif; }

h1, h2, h3 {
  font-family: "Abril Fatface", cursive;
  color: #2b2d42; }

.App {
  text-align: center; }

.App-logo {
  height: 40vmin; }

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; }

.App-link {
  color: #09d3ac; }

.reax-auth {
  height: 100vh;
  background-size: cover;
  color: #8d99ae; }
  .reax-auth-wrapper {
    height: 100vh;
    background: white;
    display: flex;
    justify-content: space-between;
    flex-direction: column; }
  .reax-auth-selection-type {
    padding-bottom: 13px;
    margin: 15px 0;
    border-bottom: 1px solid #e5d8d7; }
    .reax-auth-selection-type a {
      color: #8d99ae;
      padding: 8px 20px; }
      .reax-auth-selection-type a:hover {
        color: #2b2d42;
        cursor: pointer; }
      .reax-auth-selection-type a.active {
        color: #2b2d42;
        border-bottom: 2px solid #b5f44a;
        position: relative; }
        .reax-auth-selection-type a.active:before {
          position: absolute;
          bottom: 0;
          left: calc(50% - 4px);
          content: '';
          width: 8px;
          height: 8px;
          border: 8px solid transparent;
          border-bottom-color: #b5f44a; }
      .reax-auth-selection-type a:hover {
        text-decoration: none; }

.reax-login h1 {
  margin-bottom: 15px; }

.reax-login p {
  margin-bottom: 30px; }

.reax-login .reax-btn {
  margin-top: 15px; }

.reax-input {
  border: none;
  border-radius: 30px;
  outline: none;
  padding: 8px 15px;
  display: flex;
  width: 100%;
  margin-bottom: 15px;
  background-color: #fff2f1;
  color: #2b2d42;
  border: 1px solid #fff2f1;
  -webkit-transition: .25s all ease;
  transition: .25s all ease; }
  .reax-input:focus {
    border: 1px solid #ffd6d2; }

.reax-btn {
  border: none;
  outline: none;
  background: #94e40e;
  color: white;
  padding: 8px 20px;
  border-radius: 30px;
  box-shadow: 0 3px 6px #94e40e;
  -webkit-transition: all ease .5s;
  transition: all ease .5s; }
  .reax-btn:hover {
    background: #85cc0c; }
  .reax-btn:active {
    background: #85cc0c;
    box-shadow: none; }

.reax-sign-in h1 {
  margin-bottom: 15px; }

.reax-sign-in p {
  margin-bottom: 30px; }

.reax-sign-in .reax-btn {
  margin-top: 15px; }

.julls-app-credits {
  position: relative;
  margin-right: 15px;
  margin-bottom: 15px; }
  .julls-app-credits img {
    width: 50px;
    border-radius: 50%; }
  .julls-app-credits::before {
    display: none;
    content: 'Hecho con <3 por Jullietv';
    position: absolute;
    background: #4a4a4a;
    color: #fff;
    padding: 2px 8px;
    border-radius: 2px;
    top: calc(50% - 9px);
    width: 186px;
    font-size: .8rem;
    font-family: 'Verdana', sans-serif;
    left: 65px; }
  .julls-app-credits:hover:before {
    display: block; }

